.card-container {
  width: 400px;
}

.description {
  opacity: 0;
  transition: opacity 0.5s ease;
  background-color: rgb(176, 174, 174);
  border-radius: 25px;
  padding: 1em;
}

.card-container:hover .description {
  opacity: 1;
}

.card-container img {
  width: 50%;
  padding-top: 30px;
}
