.flex-container {
  display: flex;
  height: 75vh;
  margin-top: 50px;
}

/* .flex-item {
  flex: 1;
} */

.chapters-list li {
  margin-bottom: 1em;
}

.chapters {
  /* Style the chapters container */
  width: 20%; /* Set the width to 25% of the container */
 
  /* box-sizing: border-box; Include the padding in the width */
  /* color: #fff; */
  display: flex;
  justify-content: center;
  align-items: center;
}

.video-container {
  /* Style the video container */
  display: flex;
  width: 75%;   /* height: 80%; */
  /* border: 1px solid #fff; */
  /* justify-content: center;
  align-items: center; */


}



.chapters-list {
  margin: 0 auto;
  list-style: none;
  /* list-style-type: none; Removes default list bullet points */
padding: 0; /* Remove padding around the list */
}


.chapters-list li:hover {
color: grey;
cursor: pointer;
}


code {
display: inline-block;
padding: 6px 8px;
background-color: #f1f1f1;
color: #333;
font-family: Consolas, Monaco, 'Andale Mono', 'Ubuntu Mono', monospace;
font-size: 14px;
border-radius: 4px;
box-shadow: 0 2px 5px rgba(0, 0, 0, 0.1);
}


/* a {
color: #007bff;
text-decoration: none;
} */


a:hover {
text-decoration: underline;
}


strong {
font-weight: bold;
}


em {
font-style: italic;
}


.mdtxt {
padding-left: 20em;
padding-right: 20em;
}

img {
  /* display: block; */
  margin: 0 auto;
}